<template>
    <!--    外型框架-->
    <div>
        <div class="mdui-toolbar">
            <a href="/#/" class="mdui-btn mdui-btn-icon">
                <i class="mdui-icon material-icons">account_balance_wallet</i>
            </a>
            <span class="mdui-typo-title">青龙Tools</span>
            <div class="mdui-toolbar-spacer"></div>
            <a mdui-tooltip="{content: '刷新页面'}" href="javascript:location.reload();" class="mdui-btn mdui-btn-icon">
                <i class="mdui-icon material-icons">refresh</i>
            </a>
            <a mdui-tooltip="{content: '菜单'}" @click="this.OpenMenu()" class="mdui-btn mdui-btn-icon" id="open">
                <i class="mdui-icon material-icons">account_circle</i>
            </a>
            <a mdui-tooltip="{content: '退出登录'}" @click="this.LogOut()" id="LogOut" class="mdui-btn mdui-btn-icon" style="display: none">
                <i class="mdui-icon material-icons">exit_to_app</i>
            </a>
            <!--            菜单开始-->
            <ul class="mdui-menu" id="menu">
                <li class="mdui-menu-item" id="Admin" style="display: none">
                    <!--suppress HtmlUnknownTarget -->
                    <a href="/#/admin">管理面板</a>
                </li>
                <li class="mdui-menu-item" id="Login" style="display: inline">
                    <!--suppress HtmlUnknownTarget -->
                    <a href="/#/login" class="mdui-ripple">登录</a>
                </li>
                <li class="mdui-menu-item" id="Register" style="display: inline">
                    <!--suppress HtmlUnknownTarget -->
                    <a href="/#/register" class="mdui-ripple">注册</a>
                </li>
            </ul>
            <!--            菜单结束-->
        </div>
    </div>
</template>

<script>
import mdui from "mdui";

export default {
    name: "HeaderCom",
    methods: {
        OpenMenu(){
            const $ = mdui.$;
            const inst = this.InitMenu('#open', '#menu')
            // 打开菜单
            $('#open').on('click', function () {
                inst.open();
            });
        },
        InitMenu(as , menu){
            return new mdui.Menu(as, menu);
        },
        CheckLogin(){
            // 检查是否已登录,从本地存储里获取token
            let token = localStorage.getItem('Bearer');
            // 判断token是否为空如果为空则不跳转, 如果有则跳转到首页
            if (!(token === null || token === "")) {
                document.getElementById("Admin").style.display = "inline"
                document.getElementById("Login").style.display = "none"
                document.getElementById("Register").style.display = "none"
                document.getElementById("LogOut").style.display = "inline"
            }
        },
        LogOut(){
            // 退出登录
            console.log("退出登录")
            localStorage.clear()
            window.open("/", "_self")
        }
    },
    mounted() {
        // 注册DOM
        this.InitMenu('#open', '#menu')
        // 判断登录状态
        this.CheckLogin()
    }
}
</script>

