<template>
    <div class="box">
        <!--        公告部分-->
        <div>
            <p style="padding-bottom: 10px">网站公告（演示版）</p>
            <hr>
            <div class="notice_data">
                <!--                  开始-->
                <strong><span style="color:#E53333;">本站商品仅用于学习和调试使用，禁止用于任何违法犯罪行为</span></strong><br>
                本站主营各种:SoftWare, 按照一定的价格买服务, 不要来问非 Script 问题的问题<br>
                <strong>警告：买卖自愿/平等，买了东西就想当大爷和巨婴请自行离开，不要互相浪费时间！</strong>
                <!--                  结束-->
            </div>
        </div>
    </div>
    <div class="box" style="margin-top: 35px;">
        <div>
            <p style="padding-bottom: 10px">变量提交（演示版）</p>
            <hr>
            <div class="envInput">
                <div style="width: 100%">
                    服务器：
                    <select class="mdui-select">
                        <option v-for="d in InitData['serverData']" :key="d" :value="d['ID']">{{d['PanelName']}}</option>
                    </select>
                    &ensp;&ensp;
                    变量组：
                    <select class="mdui-select" @change="changeNum($event)">
                        <option
                            v-for="d in InitData['envData']"
                            :key="d">{{d['name']}}</option>
                    </select>
                    &ensp;&ensp;
                    剩余位置：<span id="num">0</span>
                </div>
                <div class="mdui-textfield">
                    <input type="email" class="mdui-textfield-input" placeholder="Cookie"/>
                </div>
                <div class="mdui-textfield">
                    <input type="email" class="mdui-textfield-input" placeholder="备注（可空）"/>
                </div>
                <div style="text-align: center; padding-top: 20px;">
                    <button class="mdui-btn mdui-btn-raised mdui-ripple mdui-color-blue-50" style="width: 200px;" id="LoginBtn">提&ensp;&ensp;交</button>
                </div>
            </div>

        </div>
    </div>

</template>

<script>
export default {
    name: "NoticeCom",
    computed: {
        // 渲染数据
        InitData(){
            return this.$store.state.InitIndexData
        }
    },
    methods :{
        changeNum(e){
            const numId = document.getElementById("num");
            numId.innerText = this.InitData['envData'][e.target.selectedIndex]['quantity']
        },
        changeFirstNum(){
            const numId = document.getElementById("num");
            numId.innerText = this.InitData['envData'][0]['quantity']
        },
    },
    mounted() {
        this.changeFirstNum()
    }
}
</script>

<style scoped>
.box{
    margin: 0 auto;
    width: 61.8%;
    padding: 30px;
    background: rgba(250, 249, 249, .8);
    box-sizing:border-box;
    box-shadow: 0 15px 25px rgb(60 64 67 / 25%);
    border-radius:15px;
}
@media screen and (max-width:912px){.box{width:80%;}}
@media screen and (max-width:540px){.box{width:500px;}}
@media screen and (max-width:420px){.box{width:360px;}}
@media screen and (max-width:375px){.box{width:330px;}}
@media screen and (max-width:280px){.box{width:255px;}}

.notice_data {
    text-align: center;
    line-height: 30px;
    padding-top: 10px;
}
.envInput {
    width: 70%;
    text-align: center;
    margin: 20px auto 0;
}
</style>
